.click-card {
    height: 200px;
    width: 200px;
    margin: 0.5rem;
    background-image: url("https://images.homedepot-static.com/productImages/d318e19a-6ccb-4bea-8128-1dfb6a65893a/svn/brazilwood-wilsonart-countertop-samples-mc-2x3794638-64_1000.jpg");
    padding: 5px;
    -webkit-box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.4);
            box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.4);
    -webkit-transition: -webkit-transform 0.2s, -webkit-box-shadow 0.2s;
    transition: -webkit-transform 0.2s, -webkit-box-shadow 0.2s;
    -o-transition: transform 0.2s, box-shadow 0.2s;
    transition: transform 0.2s, box-shadow 0.2s;
    transition: transform 0.2s, box-shadow 0.2s, -webkit-transform 0.2s, -webkit-box-shadow 0.2s;
}

.click-card:hover {
    -webkit-box-shadow: 7px 7px 4px rgba(0, 0, 0, 0.4);
            box-shadow: 7px 7px 4px rgba(0, 0, 0, 0.4);
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05); 
}

.card-img-top {
    height: 100%;
    width: auto;
}


